.article__img {
  margin-bottom: 1rem;
  width: 100%;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0, 0, 0, 0.08),
    0 3px 1px 0 rgba(0, 0, 0, 0.1);

  @media screen and (min-width: 530px) {
    margin-right: 1.3rem;
    margin-bottom: 0;
    float: left;
    width: 300px;
  }
}
